@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes neonPulse {
  0% {
    text-shadow: 0 0 7px #00FF85,
                 0 0 10px #00FF85,
                 0 0 21px #00FF85;
  }
  50% {
    text-shadow: 0 0 10px #00FF85,
                 0 0 15px #00FF85,
                 0 0 25px #00FF85;
  }
  100% {
    text-shadow: 0 0 7px #00FF85,
                 0 0 10px #00FF85,
                 0 0 21px #00FF85;
  }
}

@keyframes neonGlow {
  0% {
    box-shadow: 0 0 5px #00FF85,
                0 0 10px #00FF85,
                0 0 15px #00FF85;
  }
  50% {
    box-shadow: 0 0 10px #00FF85,
                0 0 20px #00FF85,
                0 0 30px #00FF85;
  }
  100% {
    box-shadow: 0 0 5px #00FF85,
                0 0 10px #00FF85,
                0 0 15px #00FF85;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.95);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

@keyframes glow {
  0%, 100% { box-shadow: 0 0 5px rgba(37, 99, 235, 0.2); }
  50% { box-shadow: 0 0 15px rgba(37, 99, 235, 0.4); }
}

@keyframes shine {
  0% { background-position: -100% 0; }
  100% { background-position: 200% 0; }
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(37, 99, 235, 0.4); }
  70% { box-shadow: 0 0 0 10px rgba(37, 99, 235, 0); }
  100% { box-shadow: 0 0 0 0 rgba(37, 99, 235, 0); }
}

@keyframes trailFade {
  0% {
    opacity: 0.8;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.3);
  }
}

.animate-fade-in {
  animation: fadeIn 0.5s ease-out forwards;
}

.animate-scale-in {
  animation: scaleIn 0.3s ease-out forwards;
}

.animate-slide-in {
  animation: slideIn 0.4s ease-out forwards;
}

.cursor-trail {
  width: 12px;
  height: 12px;
  background: #FF0099;
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: screen;
  filter: blur(1px);
  box-shadow: 0 0 8px #FF0099,
              0 0 16px #FF0099;
  animation: trailFade 0.8s linear forwards;
}

.nav-button {
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: linear-gradient(to right, transparent 50%, rgba(59, 130, 246, 0.1) 50%);
  background-size: 200% 100%;
  background-position: 0 0;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}

.nav-button:hover {
  background-position: -100% 0;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.15);
}

.nav-button:active {
  transform: translateY(0);
}

.hover-scale {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hover-scale:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
}

.hover-brightness {
  transition: all 0.3s ease;
}

.hover-brightness:hover {
  filter: brightness(1.1);
  transform: translateY(-1px);
}

.interactive-hover {
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.interactive-hover:hover {
  filter: drop-shadow(0 0 8px rgba(255, 255, 255, 0.5));
  color: white;
  text-shadow: none;
}

.interactive-hover:active {
  transform: translateY(0);
}

.float-animation {
  animation: float 3s ease-in-out infinite;
}

.icon-bounce {
  animation: float 2s ease infinite;
}

.drop-zone-active {
  border-color: #00FF85 !important;
  box-shadow: 0 0 15px #00FF85 !important;
  animation: neonGlow 1.5s infinite;
}

.copy-button {
  position: relative;
  overflow: hidden;
}

.copy-button::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transform: translate(-50%, -50%) scale(0);
  border-radius: inherit;
  transition: transform 0.3s ease;
}

.copy-button:active::after {
  transform: translate(-50%, -50%) scale(2);
}

.link-appear {
  opacity: 0;
  transform: translateY(10px);
  animation: slideUp 0.3s ease forwards;
}

@keyframes slideUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
